"use client";

import { useEffect} from "react";

import { forwardRef, useImperativeHandle, useRef, useState } from "react";
import * as XLSX from "xlsx";
import { supabase } from "../../../../lib/supabaseClient";

const BudgetUploadPage = forwardRef(function BudgetUploadPage(
  { mode = "form", selectedYear, onSuccess }, // üëà ADD onSuccess
  ref
) {

  const fileInputRef = useRef(null);

  const [category, setCategory] = useState("");
  const [description, setDescription] = useState("");
  const [amount, setAmount] = useState("");

  useImperativeHandle(ref, () => ({
    openFilePicker() {
      fileInputRef.current?.click();
    },
  }));
const [previousCategories, setPreviousCategories] = useState([]);
const [previousDescriptions, setPreviousDescriptions] = useState([]);

useEffect(() => {
  const loadCategories = async () => {
    const { data, error } = await supabase
      .from("budget_uploads")
      .select("category")
      .eq("year", 2025);

    if (!error && data) {
      const unique = [...new Set(data.map(d => d.category))];
      setPreviousCategories(unique);
    }
  };

  loadCategories();
}, []);
useEffect(() => {
  if (!category || category === "__new__") {
    setPreviousDescriptions([]);
    return;
  }

  const loadDescriptions = async () => {
    const { data, error } = await supabase
      .from("budget_uploads")
      .select("description")
      .eq("year", 2025)
      .eq("category", category);

    if (!error && data) {
      const unique = [...new Set(data.map(d => d.description))];
      setPreviousDescriptions(unique);
    }
  };

  loadDescriptions();
}, [category]);

  /* ================= FILE UPLOAD ================= */
  const handleFile = async (file) => {
    if (!file) return;

    try {
      let records = [];

      if (file.name.endsWith(".csv")) {
        const text = await file.text();
        const rows = text.trim().split(/\r?\n/).slice(1);
        records = rows.map((row) => {
          const r = row.split(",");
          return {
            year: Number(r[0]),
            category: r[1],
            description: r[2],
            amount: Number(r[3]),
          };
        });
      }

      if (file.name.endsWith(".xlsx")) {
        const buffer = await file.arrayBuffer();
        const wb = XLSX.read(buffer, { type: "array" });
        const sheet = wb.Sheets[wb.SheetNames[0]];
        const json = XLSX.utils.sheet_to_json(sheet, { defval: "" });

        records = json.map((row) => ({
  year: selectedYear, // ‚úÖ FORCE DASHBOARD YEAR
  category: row.category,
  description: row.description,
  amount: Number(row.amount),
}));

      }

      await supabase
        .from("budget_uploads")
        .upsert(records, { onConflict: "year,category,description" });
onSuccess?.(); 
      alert(`‚úÖ Uploaded ${records.length} rows`);
    } catch (err) {
      console.error(err);
      alert("‚ùå Upload failed");
    }
  };
const handleManualSubmit = async () => {
  if (!category || !description || !amount) {
    alert("‚ùå Please complete all fields.");
    return;
  }

  const record = {
    year: selectedYear, // ‚úÖ FROM DASHBOARD
    category,
    description,
    amount: Number(amount),
  };

  const { error } = await supabase
    .from("budget_uploads")
    .upsert(record, {
      onConflict: "year,category,description",
    });

  if (error) {
    console.error(error);
    alert("‚ùå Failed to save record");
    return;
  }

  alert("‚úÖ Budget item saved");
  setDescription("");
  setAmount("");
};

  return (
    <>
      {/* ALWAYS RENDER FILE INPUT (INVISIBLE) */}
      <input
        ref={fileInputRef}
        type="file"
        accept=".csv,.xlsx"
        className="hidden"
        onChange={(e) => handleFile(e.target.files?.[0])}
      />

 {mode === "form" && (
  <div className="space-y-4">

    {/* CATEGORY */}
    <div>
      <label className="text-xs font-medium text-slate-600">Category</label>
      <select
        value={category}
        onChange={(e) => setCategory(e.target.value)}
        className="border rounded px-3 py-2 text-sm w-full"
      >
        <option value="">Select Category</option>

        {previousCategories.map((c) => (
          <option key={c} value={c}>{c}</option>
        ))}

        <option value="__new__">‚ûï Add new category</option>
      </select>

      {category === "__new__" && (
        <input
          type="text"
          placeholder="Enter new category"
          onChange={(e) => setCategory(e.target.value)}
          className="border rounded px-3 py-2 text-sm w-full mt-1"
        />
      )}
    </div>

    {/* DESCRIPTION */}
    <div>
      <label className="text-xs font-medium text-slate-600">Description</label>

      {previousDescriptions.length > 0 && (
        <select
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          className="border rounded px-3 py-2 text-sm w-full"
        >
          <option value="">Select Description</option>

          {previousDescriptions.map((d) => (
            <option key={d} value={d}>{d}</option>
          ))}

          <option value="__new__">‚ûï Add new description</option>
        </select>
      )}

      {(description === "__new__" || previousDescriptions.length === 0) && (
        <input
          type="text"
          placeholder="Enter description"
          onChange={(e) => setDescription(e.target.value)}
          className="border rounded px-3 py-2 text-sm w-full mt-1"
        />
      )}
    </div>

    {/* AMOUNT */}
    <div>
      <label className="text-xs font-medium text-slate-600">Amount</label>
      <input
        type="number"
        value={amount}
        onChange={(e) => setAmount(e.target.value)}
        placeholder="Amount"
        className="border rounded px-3 py-2 text-sm w-full"
      />
    </div>

    <div className="flex justify-end">
      <button
        onClick={handleManualSubmit}
        className="bg-emerald-600 text-white px-4 py-2 rounded-lg font-semibold hover:bg-emerald-700"
      >
        üíæ Save Budget Item
      </button>
    </div>

  </div>
)}

    </>
  );
});

export default BudgetUploadPage;
